import React from "react";
import imgSuzuki from "../imgs/Suzuki.png"
import lgInstagram from "../logos/instagram.png"

export default(props) => (
    //https://wa.me/573182842772?text=Quiero+la+moto
    
    <header className="border-solid	border-b-2 border-neutral-900">
        <div className="header">
            <div>
                <img className="img-suzuki" src={imgSuzuki}></img>
            </div>
            <div className="segunda-columna-header">
                <div>
                    <h1 className="titulo-asesor">CONTACTAR ASESOR</h1>
                    <h1>{props.contacto.nombre}
                        <a href={"https://www.instagram.com/" + props.contacto.instagram}>
                            <img className="logo lg-instagram" src={lgInstagram}></img>
                        </a>
                    </h1>
                    <h1>{props.contacto.direccion}</h1>
                    <h1>{props.contacto.vitrina}</h1>
                </div>
                <div>
                <FontAwesomeIcon icon={faCoffee} size="1x" />
                    <a href="tel:+573156429192">Llamar</a>
                    <a href="https://wa.me/573182842772?text=Quiero+la+moto">Wapp</a>
                    
                </div>    
            </div>
        </div>
        
        
            
    </header>
)